html,
body {
  background: #f8f7f7;
  height: 100%;
  margin: 0;
  padding: 0;
  color: #3d3d3d;
  font: 14px Arial, Helvetica, sans-serif;
}

body {
  position: relative;
  cursor: default;
}

img {
  max-width: 100%;
}

a img {
  border: 0 none;
}

footer,
header,
nav {
  display: block;
}

a {
  text-decoration: none;
  color: #232323;
}

a:hover {
  text-decoration: underline;
}

label {
  cursor: pointer;
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
}

input[type="submit"] {
  cursor: pointer;
}

textarea {
  resize: vertical;
}

input,
textarea {
  font-family: inherit;
  color: inherit;
  outline: 0 none;
  box-sizing: border-box;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

::selection {
  color: #222;
  background: #ccd6eb;
}

::-moz-selection {
  color: #222;
  background: #ccd6eb;
}

.wrapper {
  min-height: 100%;
}

ul {
  list-style-type: none;
  padding: 0;
}

.btn,
.footer__cop,
.footer__juno {
  display: inline-block;
  vertical-align: top;
}

.contain {
  width: 930px;
  padding: 0 40px;
  margin: 0 auto;
}

.btn {
  text-decoration: none;
  text-align: center;
  color: white;
  padding: 7px 18px 0px;
  line-height: 28px;
  padding-top: 4px;
  font-size: 12px;
  border-top: 1px solid #18d373;
  border-bottom: 2px solid #0a6436;
  background: red;
  text-transform: uppercase;
}

.btn:hover {
  text-decoration: none;
}

.logo {
  margin-top: 10px;
  text-decoration: none;
}

.logo {
  margin-left: 50px;
  margin-right: 50px;
  margin-bottom: 50px;
}

.logo:hover {
  text-decoration: none;
}

.menu__list {
  display: table;
  width: 100%;
}

.menu__item {
  display: table-cell;
  background: black;
  padding: 5px;
  text-align: center;
}

.menu__item:hover,
.menu__item-active {
  background: #464646;
}

.menu__link {
  text-decoration: none;
  font-size: 18px;
  color: white;
}

.menu__link:hover {
  text-decoration: none;
}

.wrapper__footer,
.footer {
  min-height: 80px;
}

.footer {
  margin-top: -80px;
  background: gray;
}

.footer__cop,
.footer__juno {
  width: 49%;
}

.footer__cop {
  text-align: left;
}

.footer__juno {
  text-align: right;
}

.footer__juno .link {
  color: #252525;
  text-decoration: underline;
}

.footer__juno .link:hover {
  text-decoration: none;
}

.popup {
  display: block;
  opacity: 0;
  visibility: hidden;
  transition: opacity .5s , top .5s;
  position: absolute;
  left: 0;
  right: 0;
  z-index: 999999;
  bottom: 0;
  top: 0;
}

.popup__wp {
  max-width: 736px;
  background: #fff;
  box-sizing: border-box;
  margin: 85px auto 85px;
  box-shadow: 0 1px 9px rgba(30,15,0,0.53);
}

.overlay:target {
  visibility: visible;
  opacity: 1;
}

.overlay:target+.popup {
  opacity: 1;
  visibility: visible;
}

.overlay {
  visibility: hidden;
  background: rgba(80,80,80,0.5);
  bottom: 0;
  opacity: 0;
  z-index: 99999;
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
  cursor: default;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
}

.popup__top {
  text-align: right;
  height: 60px;
}

.popup__content .title {
  font-weight: bold;
}

.popup__text {
  margin-bottom: 28px;
}

.popup__content {
  padding: 0 50px 50px 50px;
  font-size: 15px;
}

.icon-close {
  display: inline-block;
  text-decoration: none;
  margin: 25px;
  height: 18px;
  width: 18px;
  background: url("../img/icon-close.png") no-repeat;
}

.form__form {
  margin-top: 30px;
}

.form__row {
  margin: 10px 0 11px 0px;
  position: relative;
}

.form__label {
  display: inline-block;
  vertical-align: top;
  width: 227px;
  font-size: 16px;
  margin-top: 11px;
}

.form__input {
  box-sizing: border-box;
  border: 1px solid black;
  width: 295px;
  height: 34px;
  line-height: 34px;
  font-size: 16px;
  font-family: 'PT Sans';
  padding: 0 10px;
  background: white;
}

.form__input:hover,
.form__input:focus {
  outline: none;
}

.form__textarea {
  height: 240px;
}

.form__submit {
  font-size: 20px;
  display: block;
  margin: 0 auto;
  margin-top: 36px;
  transition: background .3s;
}

.form__file {
  font-size: 16px;
  text-decoration: underline;
  color: black;
  text-transform: uppercase;
  font-weight: 700;
  font-family: 'PT Sans';
  cursor: pointer;
}

.form__file {
  cursor: pointer;
}

.form__file {
  width: 345px;
  margin-top: 12px;
}

.radios label {
  margin-right: 60px;
  font-family: 'PT Sans';
}

.radios {
  margin: 34px 0;
}

.slider-main {
  height: 360px;
  overflow: hidden;
  position: relative;
}

.slider-main__block {
  position: relative;
  background: no-repeat;
  background-size: cover;
  height: 360px;
  width: 100%;
}

.slider-main__text {
  color: #4e5366;
  font-size: 18px;
}

.slider-main__title {
  color: black;
  text-transform: uppercase;
  font-size: 35px;
  margin-bottom: 28px;
}

.slider-main__content {
  padding-left: 105px;
  padding-top: 120px;
}

.slider-item__item {
  width: 215px;
  display: block;
  text-decoration: none;
}

.slider-item__img {
  border-radius: 3px;
}

.slider-item__title {
  font-size: 18px;
}
